* {
  box-sizing: border-box;
}

.header {
  position: absolute;
  height: 0px;
  top: 40px;
  min-width: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin: 0 auto;
  z-index: 10;

}

.fixed {
  height: 62px;
  //min-width: 300px;
  display: flex;
  //flex-direction: column;
  align-items: center;
  justify-content: center;
  min-width: 100%;
  margin: 0 auto;
  z-index: 10;
  position: fixed;
  transition: height 0.2s ease-out;
  top: 0;
  background-color: #ffffff;
  box-sizing: border-box;
  box-shadow: 0 5px 4px rgba(0, 0, 0, 0.2);
}

nav {
  width: 400px;
  min-width: 298px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  z-index: 2;
  align-items: center;
}

.backgroundPhoto {
  width: 100%;
  height: auto;
  //background-image: url("https://disk.yandex.ru/d/bCiHO9-t-Il-Kg/ausiannikau.artyom-23.jpg");
  min-width: 370px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% -25px;

  &:before {
    position: absolute;
    content: "";
    left: 0;
    bottom: -1px;
    right: 0;
    top: 0;
    background: rgba(0, 0, 0, 0.4);
    min-width: 370px;
    height: 100vh;
  }
}

.welcomeContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  flex-direction: column;
}

.welcomeBlock {
  text-align: center;

  & h1 {
    z-index: 1;
    font-size: 50px;
    color: rgba(255, 255, 255, 0.85);
    font-weight: 900;
    line-height: 1;
  }

  & p {
    z-index: 1;
    color: rgba(255, 255, 255, 0.85);
    font-size: 1.2rem;

    & a {
      z-index: 1;
      color: #96bb7c;
      font-weight: 700;
      font-size: 1.2rem;
    }
  }
}

.burgerMenuIcon {
  cursor: pointer;
  width: 40px;
  height: 40px;
  z-index: 1;
}

.headerBlock {
  position: absolute;
  height: 52px;
  display: flex;
  width: 100%;
  padding: 15px 10px 20px 0;;
  min-width: 360px;
  justify-content: flex-end;
}

.fixedHeaderBlock {
  height: 52px;
  display: flex;
  width: 100%;
  align-items: center;
  padding: 0 10px;
  z-index: 999;
  min-width: 360px;
  margin: 0 auto;
  justify-content: flex-end;
  position: fixed;
  transition: height 0.5s ease-out;
  text-align: center;
  background-color: white;
  box-shadow: 0 5px 4px rgba(0, 0, 0, 0.2);
}
.fixedHeaderDark {
  background-color: #212020;
}


@media screen and (max-width: 768px) {
  .headerContainer {
    justify-content: center !important;
  }

  .welcomeBlock {
    text-align: center;

    & h1 {
      z-index: 1;
      font-size: 30px;
      color: rgba(255, 255, 255, 0.8);
      font-weight: 900;
      line-height: 1;
    }

    & p {
      z-index: 1;
      color: rgba(255, 255, 255, 0.9);
      font-size: 1.2rem;

      & a {
        z-index: 1;
        color: #96bb7c;
        font-weight: 700;
        font-size: 1.2rem;
      }
    }
  }
}

@media screen and (max-width: 1000px) {
  .backgroundPhoto {
    background-position: 50%;
  }
}






